* {
  box-sizing: border-box;
}

html, body {
  max-width: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

#root {
  max-width: 100%;
  margin: 0;
}

#phonebook {
  max-width: 100%;
  min-height: 100vh;
  display: flex;
  margin: 0;
}

.message {
  flex: 1 0 100%;
  width: 100%;
  text-align: center;
  font-size: 1.4em;
  padding: 8px;
  margin: 0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 500;
}

.message-success {
  background-color: #d9f1d9;
  color: #008000;
}

.message-warning {
  background-color: #f3f3b0;
}

.message-error {
  background-color: #f8d7d7;
  color: #ff0000;
}

#left {
  flex: 1 0 400px;
  max-width: 400px;
  margin: 0;
  padding: 32px;
  background-color: #663399;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
}

#left > h1 {
  display: none;
  margin: 0 0 24px;
  text-align: center;
  color: #dddddd;
}

#search-filter {
  width: 100%;
  margin: 0 0 32px;
}

form {
  max-width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
}

form > h2 {
  width: 100%;
  margin: 32px 0;
  color: #f0f0f0;
  text-align: center;
}

input, button[type="submit"] {
  display: block;
  width: 100%;
  margin-bottom: 24px;
  outline: 0;
  border: none;
  border-radius: 4px;
  padding: 16px 24px;
  font-size: 1.2em;
}

input[type="search"] {
  margin-top: 10px;
  margin-bottom: 0;
}

input {
  background-color: lavender;
}

button[type="submit"] {
  background-color: #ddaa13;
  color: #333333;
  cursor: pointer;
}

#contacts {
  flex: 1 0 auto;
  max-width: 100%;
  margin: 0 0 0 400px;
  padding: 32px;
  background-color: #f9f9f9;
}

#loading {
  color: #888282;
}

#contacts > h1 {
  margin-top: 0;
  padding: 0;
  color: #222222;
}

.contact {
  margin-bottom: 8px;
  border-radius: 4px;
  border: 1px solid #cccccc;
  padding: 8px;
  display: flex;
  align-items: flex-start;
  position: relative;
}

.avatar {
  flex: 0 0 32px;
  max-width: 32px;
  max-height: 32px;
  margin: 5px 8px 0 0;
  transition: all .3s ease;
}

.contact-details {
  flex: 0 0 auto;
  margin: 0 32px 0 0;
  padding: 0;
}

.contact:hover {
  border: none;
  background-color: #e6d7f6;
}

.contact-name {
  margin: 0;
  font-size: 1.3em;
}

.contact-number {
  margin: 0;
  font-size: 1.1em;
}

.delete-contact {
  flex: 0 0 24px;
  max-width: 24px;
  position: absolute;
  top: 8px;
  right: 8px;
  opacity: 0.7;
  cursor: pointer;
}

.delete-contact:hover {
  opacity: 1;
  box-shadow: 0 0 8px 2px rgba(0, 0, 0, 0.2);
}

.delete-contact:active {
  transform: scale(1.1);
}

.empty-list {
  max-width: 400px !important;
}

@media screen and (max-width: 800px) {
  #left, #contacts {
    flex: 0 0 50%;
    max-width: 50%;
    padding: 24px;
  }

  #contacts {
    margin-left: 50%;
  }
}

@media screen and (max-width: 768px) {
  #phonebook {
    flex-wrap: wrap;
  }

  #left, #contacts {
    flex: 1 0 100%;
    max-width: 100%;
  }

  #left {
    position: static;
  }

  #contacts {
    margin-left: 0;
  }

  #left > h1 {
    display: block;
  }

  #contacts > h1 {
    display: none;
  }

  form {
    margin: 0;
  }

  form > h2 {
    margin: 0 0 16px;
  }

  input[type="search"] {
    margin-top: 0;
  }
  
  input, button {
    max-width: 400px;
    display: block;
  }

  input {
    margin: 16px auto;
  }

  button[type="submit"] {
    margin: 16px auto 0;
  }
}

@media screen and (max-width: 392px) {
  #left, #contacts {
    padding: 16px 8px;
  }
}